x 1px 3px 0px rgba(0, 0, 0, 0.2);\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.project.selected,\r\nform input:focus,\r\nform select:focus,\r\nform textarea:focus {\r\n  outline: 2px solid var(--main-color);\r\n}\r\n\r\n.new-project {\r\n  border: 1px dashed rgba(0, 0, 0, 0.4);\r\n  opacity: 0.75;\r\n}\r\n\r\n.project-icon {\r\n  border-radius: 50%;\r\n}\r\n\r\n.todo-count {\r\n  background-color: #ddd;\r\n  color: #3d3d3d;\r\n  text-align: center;\r\n  border-radius: 10px;\r\n}\r\n\r\n.todo,\r\n.new-todo {\r\n  border-bottom: 1px solid rgba(150, 150, 150, 0.5);\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.date {\r\n  display: inline;\r\n  color: #3d3d3d;\r\n  background-color: #ddd;\r\n  border-radius: 5px;\r\n  font-weight: bold;\r\n}\r\n\r\n.prio-high {\r\n  color: var(--prio-high-color);\r\n}\r\n\r\n.prio-med {\r\n  color: var(--prio-med-color);\r\n}\r\n\r\n.prio-low {\r\n  color: var(--prio-low-color);\r\n}\r\n\r\nbutton,\r\ninput,\r\nselect,\r\ntextarea {\r\n  outline: none;\r\n  border: none;\r\n  background: none;\r\n}\r\n\r\nform input,\r\nform select,\r\nform textarea {\r\n  border-radius: 5px;\r\n  border: 1px solid #dcdee2;\r\n}\r\n\r\n.todo-edit-btn,\r\n.todo-delete-btn,\r\n.project-edit-btn,\r\n.project-delete-btn {\r\n  border-radius: 5px;\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.edit-todo {\r\n  border-left: 1px solid rgba(150, 150, 150, 0.5);\r\n}\r\n\r\nlabel {\r\n  font-weight: bold;\r\n}\r\n\r\nform#edit-todo > button,\r\nform#edit-project > button {\r\n  font-weight: bold;\r\n  white-space: nowrap;\r\n  padding: 10px 20px;\r\n  margin: 20px 40px 20px 40px;\r\n  background-color: var(--main-color);\r\n  border-radius: 5px;\r\n  border: none;\r\n  box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.25);\r\n}\r\n\r\nform#edit-todo > button:hover,\r\nform#edit-project > button:hover {\r\n  background-color: var(--main-color-hover);\r\n  cursor: pointer;\r\n}\r\n\r\nform#edit-todo > button:active,\r\nform#edit-todo > button:focus,\r\nform#edit-project > button:active,\r\nform#edit-project > button:focus {\r\n  background-color: var(--main-color-active);\r\n  outline: none;\r\n}\r\n\r\n.project:hover,\r\n.new-project:hover,\r\n.todo:hover,\r\n.new-todo:hover,\r\n.new-todo > input:hover,\r\n.new-todo > button:hover,\r\n.project-edit-btn:hover,\r\n.project-delete-btn:hover {\r\n  background-color: #ececec;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.new-project:hover {\r\n  border-style: solid;\r\n}\r\n\r\n.new-todo > input:focus {\r\n  cursor: text;\r\n}\r\n\r\n.new-todo-btn > span {\r\n  transition: color 0.2s ease;\r\n}\r\n\r\n.new-todo-btn:hover > span {\r\n  color: var(--main-color);\r\n}\r\n\r\n.todo-edit-btn:hover,\r\n.todo-delete-btn:hover {\r\n  background-color: var(--main-color);\r\n  cursor: pointer;\r\n}\r\n\r\n.todo > button.todo-done-btn > span::after {\r\n  content: \"circle\";\r\n}\r\n\r\n.todo > button.todo-done-btn > span:hover::after {\r\n  cursor: pointer;\r\n  content: \"check_circle\";\r\n}\r\n\r\n.todo.done > button.todo-done-btn > span::after {\r\n  content: \"check_circle\";\r\n  font-variation-settings: \"FILL\" 1;\r\n  transition: 0.1s ease-in;\r\n}\r\n\r\n.todo.done > .todo-title,\r\n.todo.done > .todo-date {\r\n  text-decoration: line-through;\r\n}\r\n\r\n.todo.done {\r\n  opacity: 0.5;\r\n}\r\n\r\n.todo.done > button.todo-edit-btn,\r\n.todo.done > button.todo-delete-btn {\r\n  display: none;\r\n}\r\n\r\n/* Modal */\r\n\r\n.modal {\r\n  display: none; \r\n  position: fixed;\r\n  z-index: 1;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.4);\r\n  animation: fade-in 0.3s;\r\n}\r\n\r\n.modal .modal-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  background-color: #fefefe;\r\n  margin: 30vh auto;\r\n  padding: 20px;\r\n  border-radius: 5px;\r\n  width: fit-content;\r\n  animation: fade-in 0.3s;\r\n}\r\n\r\n@keyframes fade-in {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
